import React, { useEffect } from 'react';
import SobreMim from './components/SobreMim';
import Projetos from './components/Projetos';
import Inicio from './components/Inicio';
import Contatos from './components/Contatos'
import './App.css';

function App() {
  const projects = [
    {
      title: 'Insero de Placas em Telhados 2D',
      languages: ['JavaScript', 'HTML', 'CSS'],
      description: 'Descrio: O sistema permite inserir placas em uma imagem do Google Maps ou Earth, ajustando as dimenses do telhado e das placas.  possvel definir a distncia entre as placas, recortar a imagem, rotacion-la, e desenhar restries para evitar sobreposio de placas. Aps as configuraes, o sistema gera duas imagens com as placas dispostas em formato retrato e paisagem.',
      githubLink: 'https://github.com/GuaraciTech/Solaresp-Code',
    },
    {
      title: 'Gerenciador de Alimentos',
      languages: ['Node.js', 'Express.js', 'MongoDB', 'Moongose'],
      description: 'Descrio: Gerenciador de Alimentos onde cria, lista, busca, atualiza e exclui. Para criao  necessrio os seguintes dados: id, nome, categoria, quantidade disponvel, data de validade e preo do alimento.',
      githubLink: 'https://github.com/pedro-rafaiel/gerenciador_de_alimentos',
    },
    {
      title: 'Clinicamed',
      languages: ['MySQL'],
      description: 'Descrio: Script SQL completo para a criao e gerenciamento de um sistema de banco de dados para uma clnica mdica. O objetivo  gerenciar pacientes, mdicos, clnicas, consultas, horrios disponveis e exames de forma eficiente.',
      githubLink: 'https://github.com/pedro-rafaiel/clinicamed',
    },
    {
      title: 'Ordenador',
      languages: ['C'],
      description: 'Descrio: Implementao de operaes sobre conjuntos disjuntos e ordenao de subconjuntos utilizando diferentes algoritmos de ordenao. O programa l dados de um arquivo de entrada e oferece um menu interativo para o usurio realizar diversas operaes, como unio de conjuntos, busca de representantes e ordenao de subconjuntos.',
      githubLink: 'https://github.com/pedro-rafaiel/ordenar',
    },
    {
      title: 'Agenda Hospital',
      languages: ['C'],
      description: 'Descrio: Simula o agendamento e gerenciamento de consultas mdicas. Ele aloca pacientes para consultas com base em prioridades, gerencia faltas com um sistema de substituio de banco de reservas, e gera relatrios detalhados sobre o processo.',
      githubLink: 'https://github.com/pedro-rafaiel/agenda_hospital',
    },
    {
      title: 'RPG (Em andamento)',
      languages: ['C#'],
      description: 'Descrio: Joguinho simples de uma luta apenas por enquanto, de um heri contra um goblin, onde voc  o heri e pode se defender ou atacar, baseado nos dados de 1 a 10 vai decidir o seu dano e o do inimigo.',
      githubLink: 'https://github.com/pedro-rafaiel/RPG',
    },
  ];

  useEffect(() => {
    const createSparkle = (e) => {
      const sparkle = document.createElement('div');
      sparkle.classList.add('sparkle');
      document.body.appendChild(sparkle);

      sparkle.style.left = `${e.clientX}px`;
      sparkle.style.top = `${e.clientY}px`;

      setTimeout(() => {
        sparkle.remove();
      }, 1000);
    };

    window.addEventListener('mousemove', createSparkle);

    return () => {
      window.removeEventListener('mousemove', createSparkle);
    };
  }, []);

  return (
    <div className="App">
      <Inicio />
      <SobreMim />
      <Projetos projects={projects} />
      <Contatos />
    </div>
  );
}

export default App;
